<template>
	<aside>
		<div class="uk-inline uk-height-viewport">
			<div id="sidebar-brand" class="uk-padding-small uk-margin-top uk-margin-medium-bottom">
				<router-link :to="{name: 'home'}" class="uk-logo uk-flex uk-flex-center">
					<img src="@/assets/img/logo-inverse.png" :alt="'NAME' | translate" width="50%" />
				</router-link>
			</div>

			<div id="sidebar-user" class="uk-margin-medium-bottom uk-padding uk-padding-remove-vertical">			

				<!-- If you are logged in -->
				<div v-if="username">
					<h4 class="uk-margin-remove">Nombre {{ username }}</h4>
					<ul class="uk-subnav uk-margin-remove" uk-margin>
						<li>
							<div class="uk-navbar-dropdown">
								<ul class="uk-nav uk-navbar-dropdown-nav">
									<li>
										<router-link :to="{ name: 'profile' }">{{'PROFILE' | translate}}</router-link>
									</li>
									<li>
										<a @click="logout">{{'LOGOUT' | translate}}</a>
									</li>

									<li class="uk-nav-header">{{'SELECT_LANGUAGE' | translate}}</li>
									<li v-for="(language, index) in availableLanguages" :key="index">
										<input
										type="radio"
										:id="language.id"
										:value="language.id"
										v-model="selectedLanguage"
										/>
										<label :for="language.id">{{language.label}}</label>
									</li>

								</ul>
							</div>
						</li>
					</ul>
				</div>

				<!-- If you are not logged in -->
				<div v-else>
					<a href="#modal-login" uk-toggle class="uk-h4">
						{{'LOGIN_TITLE' | translate}}
					</a>

				<!--<li v-for="(language, index) in availableLanguages" :key="index">
					<a href>
						<input
						type="radio"
						:id="language.id"
						:value="language.id"
						v-model="selectedLanguage"
						/>
						<label :for="language.id">{{language.label}}</label>
					</a>
				</li>-->
			</div>
			
		</div>

		<nav id="sidebar-navigation" class="uk-margin-medium-bottom uk-padding  uk-padding-remove-vertical">
			<ul class="uk-nav uk-nav-default">
				<li>
					<router-link :to="{name: 'home'}">{{'HOME' | translate}}</router-link>
				</li>
				<li>
					<router-link to="/series">{{'SERIES' | translate}}</router-link>
				</li>
				<li>
					<router-link to="/genres">{{'GENRES' | translate}}</router-link>
					<div class="uk-navbar-dropdown">
						<ul class="uk-nav uk-navbar-dropdown-nav">
							<li v-for="genre in genres" v-bind:key="genre.id">
								<router-link :to="{ path: '/genre/' + genre.id }" :key="genre.id">
									{{ genre.name }}
								</router-link>
							</li>
						</ul>
					</div>
				</li>
				<li>
					<router-link to="/authors">{{'AUTHOR_ALL' | translate}}</router-link>
				</li>
				<li>
					<router-link to="/shop">{{'SHOP' | translate}}</router-link>
				</li>
				<li>
					<a href="#modal-full" uk-toggle @click="initSearch">Buscar</a>
				</li>
				<li class="uk-hidden">
					<a class="uk-navbar-toggle" uk-navbar-toggle-icon uk-toggle="target: #offcanvas-nav"></a>
				</li>
			</ul>
		</nav>

		<div id="sidebar-footer" class="uk-padding uk-margin-bottom uk-padding-remove-vertical">
			<Footer />
		</div>

		<!-- <NavGenre v-if="showGenre"></NavGenre> -->
		<Offcanvas></Offcanvas>

		<div id="modal-full" class="uk-modal-full uk-modal" uk-modal>
			<div class="uk-modal-dialog uk-flex uk-flex-center uk-flex-middle" uk-height-viewport>
				<button class="uk-modal-close-full" type="button" uk-close ref="email"></button>
				<form id="it-search" class="uk-search uk-search-large" v-on:submit.prevent="search">
					<input
					class="uk-search-input uk-text-center"
					type="search"
					v-model="searchText"
					:placeholder="'SEARCH_PLACEHOLDER' | translate"
					autofocus
					/>
				</form>
			</div>
		</div>

		<Login></Login>
	</div>	
</aside>
</template>

<script>
//import NavGenre from "./NavGenre.vue";
import Footer from "./Footer.vue";
import Offcanvas from "./Offcanvas.vue";
import Login from "../containers/users/Login.vue";
import { mapMutations, mapGetters } from "vuex";
import languages from "../assets/localization/languages";
import strapi from "../../utils/Strapi";
import { NAV_ALL_CATEGORIES, NAV_GENRE_ALL_GENRES}  from '../constants/graphql';

export default {
	name: "Nav",
	mounted: function () {
		this.loadUsername();
	},
	methods: {
		...mapMutations("i18n", ["setCurrentLanguage"]),
		initSearch() {
			this.searchText = "";
		},
		search() {
			this.$refs.email.click();
			this.$router.push("/search?q=" + this.searchText);
		},
		logout() {
			this.$store.dispatch("auth/logout");
			this.loadUsername();
			this.$router.go();
		},
		loadUsername() {
			setTimeout(() => {
				this.username = this.$store.getters["auth/getUsername"];
				this.userId = this.$store.getters["auth/getId"];
			}, 1000);
		},
		async saveUserPreference(preferenceLanguage) {
			await strapi.updateEntry("users", this.userId, {
				language: preferenceLanguage,
			});
		},
	},
	components: {
		Offcanvas,
		Footer,
		Login,
	},
	computed: {
		showGenre() {
			return this.$route.name != "serie" && this.$route.name != "profile";
		},
		...mapGetters("i18n", ["currentLanguage"]),
		selectedLanguage: {
			get() {
				return this.currentLanguage;
			},
			set(value) {
				/* save user preference language */
				this.saveUserPreference(value);
				this.setCurrentLanguage(value);
			},
		},
	},
	data() {
		return {
			categories: [],
			username: null,
			userId: null,
			availableLanguages: languages,
			searchText: this.$route.query.q,
		};
	},
	apollo: {
		categories: NAV_ALL_CATEGORIES,
		genres: NAV_GENRE_ALL_GENRES
	},
};
</script>