<template>
	<div id="it-slide-featured">
		<div class="uk-position-relative uk-visible-toggle" tabindex="-1" uk-slideshow="animation: pull">
			<ul class="uk-slideshow-items">
				<li v-for="serie in series" :key="serie.id">
					<section class="it-serie-hero">
						<div class="uk-container uk-container-large">
							<div class="uk-flex-middle" uk-grid >
								<div class="uk-width-3-5@m uk-width-1-2@l it-serie-info uk-padding">
									<!-- Serie Genres -->
									<p v-if="serie.genres" class="it-serie-genre">
										<span v-for="genre in serie.genres" :key="genre.id">
											{{ genre.name }}
										</span>
									</p>

									<!-- Serie Title -->
									<h1 class="it-serie-title uk-article-title">{{ serie.Title }}</h1>

									<!-- Serie Author -->
									<h2 class="it-serie-author uk-text-large uk-margin-remove-top">
										<span v-for="author in serie.authors" :key="author.id">
											<router-link :to="{ path: '/author/' + author.id }" :key="author.id" class="uk-link-reset" >
												{{ author.name }}
											</router-link>
										</span>
									</h2>

									<vue-markdown-it v-if="serie.Description" :source="serie.Description" class="it-serie-description uk-visible@m"></vue-markdown-it>

									<nav class="it-serie-actionbar uk-flex uk-flex-middle">
										<router-link :to="{ path: '/serie/' + serie.id }" class="it-button-readnow uk-button uk-button-large uk-button-dark" >
											{{'SERIE_READ' | translate}}
										</router-link>
									</nav>
								</div>
							</div>
						</div>
					</section>
				</li>
			</ul>

			<a class="uk-position-center-left uk-position-small uk-hidden-hover" href="#" uk-slidenav-previous uk-slideshow-item="previous"></a>
			<a class="uk-position-center-right uk-position-small uk-hidden-hover" href="#" uk-slidenav-next uk-slideshow-item="next"></a>
		</div>
	</div>
</template>

<script>
	import { FEATURE_LIST_ALL_FEATURED }  from '../constants/graphql';
	import VueMarkdownIt from "vue-markdown-it";
	
	export default {
		name: "FeatureSlide",
		mounted: function() {
			setTimeout(() => {
				this.id = this.$store.getters["auth/getId"];
			}, 100);
		},
		data() {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				series: [],
				id: null
			};
		},
		components: {
			VueMarkdownIt
		},
		apollo: {
			series: {
				query: FEATURE_LIST_ALL_FEATURED,
				variables() {
					return { userId: this.id };
				},
				skip() {
					return this.id !=null;
				}
			}
		}
	};
</script>